@import "./mixins.scss";

.anm1 {
    animation: anim1 1s ease-in;
    animation-delay: 2s;
    color: goldenrod;
}
.anm2 {
    margin: 0px 10px;
    animation: anim2 2s ease-in;
    animation-delay: 2s;
    color: goldenrod;
}

.main-header {
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    background-color: #333;
    color: #fff;
    align-items: center;
    line-height: 70px;

    .left {
        font-size: 32px;
        // animation: anim1 5s infinite;
        .anm1 {
            animation: anim1 1s ease-in;
            animation-delay: 2s;
        }
        .anm2 {
            margin: 0px 10px;
            animation: anim2 2s ease-in;
            animation-delay: 2s;
        }
    }

    .center {
        display: none;
        gap: 20px;
        list-style: none;
        cursor: pointer;
        
        li {
            a {
                color: white;
                text-decoration: none;

                &:hover {
                    color: #e4d7d7;
                }
            }
        }


        @include md {
            display: flex;
        }

        // .nav-item {
        //     margin: 0 20px;
        //     cursor: pointer;
        //     transition: color 0.3s ease;

        //     &:hover {
        //         color: #ffd700;
        //     }
        // }
    }


    .right {
        display: flex;
        gap: 20px;
    }
}

