@keyframes anim1 {
    from {
        transform: rotate(0deg);
        color: goldenrod;
    }
    to {
        transform: rotate(360deg);
        color: white;
    }
}
@keyframes anim2 {
    0% {
        margin: 0px 10px;
        color: goldenrod;
    }
    50% {
        margin: -10px;
        color: whitesmoke;
    }
    100% {
        margin: 0px 10px;
        color: goldenrod;
    }
}